<template>
  <view
    class="flex items-center user-info to-primary-300 from-primary-500 px-4 h-30"
    @click="handleLogin"
  >
    <uv-avatar
      :src="showAvatar"
      size="80"
      @click="previewImage(userInfo.avatar_url)"
      mode="aspectFill"
    ></uv-avatar>
    <view class="ml-4 flex-1">
      <view v-if="isLogin" class="info">
        <view>
          <view class="u-cnetent flex items-center">
            <text class="u-name uv-line-1 text-black mr-5 text-xl">{{ userInfo.nick_name }}</text>
            <LevelIcon v-if="showLevel" :level="userInfo.level" />
          </view>
          <view>
            <text class="u-id text-sm">{{ userInfo.phone }}</text>
          </view>
          <view class="flex">
            <view v-for="(item, index) in userInfo.tag_ids_arr" :key="index" class="mr-6rpx">
              <uv-tags :text="item.tag_id_str" size="mini" type="success"></uv-tags>
            </view>
          </view>
        </view>
      </view>
      <view v-else>立即登录</view>
    </view>
    <view style="flex-shrink: 0" class="h-15">
      <uv-icon :name="edit" color="#faac0e" size="30" class="icon" @click="toEdit"></uv-icon>
    </view>
  </view>
</template>

<script>
import { previewImage } from '@/utils/index'
import LevelIcon from '@/components/LevelIcon/index.vue'
import defaultAvatar from '@/static/images/avatar.png'
import edit from '@/static/images/personal/edit.png'
export default {
  name: 'userInfo',
  components: {
    LevelIcon,
  },
  props: {},
  data() {
    return {
      edit,
      previewImage: previewImage,
    }
  },
  computed: {
    isLogin() {
      return !!this.$store.user.token
    },
    userInfo() {
      return this.$store.user.userInfo
    },
    showAvatar() {
      return this.userInfo.avatar_url || defaultAvatar
    },
    themeType() {
      return this.$store.app.themeType
    },
    showLevel() {
      return this.userInfo.is_auth_instructor && this.themeType == 1
    },
  },
  methods: {
    toEdit() {
      uni.navigateTo({
        url: '/pages/personalDetail/index',
      })
    },
    handleLogin() {
      if (this.isLogin) {
        return
      }
      uni.navigateTo({
        url: '/pages/login/index',
      })
    },
  },
}
</script>

<style scoped lang="scss">
.user-info {
  opacity: 1;
  z-index: 1;
  .uv-cnetent {
  }

  .uv-level {
  }

  .uv-id {
    color: rgba(165, 173, 186, 1);
  }
}
</style>
